.input:focus {
  border: 1px solid black;
  opacity: 1;
  font-weight: 500;
  font-size: 16px;
  color: #000;
  border-radius: 3px;
}

.input {
  width: 372px;
  height: 36px;
  border-radius: 3px;
}

.inputWrapper {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.superInput {
  opacity: 0.5;
}

.errorInput {
  border: 1px solid #cc1439;
}

.errorInput:focus {
  font-size: 14px;
}

.error {
  font-weight: 400;
  font-size: 10px;
  color: #cc1439;
}
