.greetingForm {
}

.inputAndButtonContainer {
  display: flex;
  gap: 10px;
  margin: 10px 0;
}

.error {
  margin-top: 10px;
  font-weight: 400;
  font-size: 14px;
  color: #c00;
}

.input {
  width: 372px;
  height: 36px;
  opacity: 0.5;
  border-radius: 3px;
}

.input:focus {
  border: 1px solid black;
  opacity: 1;
  font-weight: 500;
  font-size: 16px;
  color: #000;
  border-radius: 3px;
}

.errorInput {
  width: 372px;
  height: 36px;
  border-radius: 3px;
  border: 1px solid #c00;
}

.button {
  border-radius: 3px;
  padding: 5px 24px;
  width: 78px;
  height: 36px;
  background: #06c;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.42857;
  color: #fff;
  border: none;
  cursor: pointer;
}

.button:focus {
  outline: none;
}

.button:active {
  transform: scale(0.98);
}

.button:disabled {
  background: #06c;
  opacity: 0.5;
}

.text {
  font-weight: 400;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.5);
}

